header{
    min-height: 70px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
}
header .logo{
    text-decoration: none;
    text-transform: uppercase;
    color: black;

}


nav ul li{
    list-style: none;
    display: inline-block;
    

}
nav ul{
    margin-left: -15%;
}
nav ul li a{
    text-decoration: none;
    text-transform: uppercase;
    color: #555;
    padding: 0 15px;
}
nav ul li a hover{
    color: lightseagreen;
}
nav .nav-cart{
    cursor: pointer;
    position: relative;
    top: -25px;

}
nav .nav-cart span{
    position: absolute;
    top: -24px;
    right: -12px;    
    background: crimson;
    font-size: 12px;
    color: white;
    padding: 3px 5px;
    border-radius: 50%;
    z-index: -1;
}
.cartimg{
    position: absolute;
    top: -13px;    
    right: -12px;     
    padding: 3px 5px;  
    
}

header .menu,
header .close{
    cursor: pointer;
    display: none;
}
@media (max-width:650px){
    header ul{
        position: fixed;
        top: 100%;
        left: 0;
        width: 115%;
    
        height: 20vh;
        display:inline-flex;
        justify-content:space-around;
        align-items: center;
        flex-direction: column;
        background: white;
        z-index: 99;
        opacity: 0.97;
        transition: 0.5s linear;
    }
   

    header .menu,
    header .close{
        display: block;
    }
    header .close{
        position: absolute;
        top: 10px;
        right: 20px;

    }
    header ul.toggle{
        top: 0;
    }
}
